/* Discount */
.amazon-discount .discount-label {
    font-weight: bold;
    color: white;
    font-size: 0.96em;
    vertical-align: middle;
    background-color: #00772f;
    padding: 1px 6px;
    border-radius: 4px;
    box-sizing: border-box;
}

/* Image Preview Tooltip */
.aal-image-preview-tooltip .wp-pointer-arrow {
    display: none;
}
.aal-image-preview-tooltip .wp-pointer-content {
    padding: 12px;
    border-radius: 0.4em;
    /*max-height: 100%; */
    /* Center child elements  */
    display: flex;
    align-items: center;
    justify-content: center;
    height: inherit;
    width: inherit;
}
.aal-image-preview-tooltip .wp-pointer-content a {
    height: inherit;
    width: inherit;
    max-height: 100%;
    max-width: 100%;
    margin: 0;
    padding: 0;
    display: block;
    display: flex;
    align-items: center;
    justify-content: center;
}
.aal-image-preview-tooltip .wp-pointer-content img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    /* Center image */
    vertical-align: middle;
    display: block;
    margin: auto;
}

/* Pointer Tooltip */
.aal-tooltip .wp-pointer-content {
    padding: 1em;
    border-radius: 0.4em;
    color: #444;
}
.aal-tooltip .wp-pointer-arrow {
    display: none;
}

/* Rating stars */
span.crAvgStars a {
    text-decoration: none;
    border-bottom: 0;
    /* No underlines in hyper links with box shadow */
    box-shadow: none;
}
.review-stars svg {
    width: auto;
    height: 1em;
    vertical-align: middle;
    position: relative;
    top: -1px;
}

/* Prime icon */
.amazon-prime svg {
    position: relative;
    height: 1.2em;
    vertical-align: middle;
}

svg.definition {
    display: none;
}

.review-stars {
    white-space: nowrap;
    display: inline-block; /* for white-space: nowrap to take effect*/
}